  --black0: rgba(51,51,51,0);
  --black: rgba(51,51,51,1);
  --black15: rgba(51,51,51,.15);
  --black40: rgba(51,51,51,.4);
  --black50: rgba(51,51,51,.5);
  --black60: rgba(51,51,51,.6);
  --red: rgba(238,85,75,1);
  --green: rgba(132,183,57,1);
  --purple: rgba(108,57,183,1);
  --gray-light: rgba(249,249,249,1);
}

